<template>
  <div>

    <div class="document-main-box" id="documentMainBox">

      <div class="document-main-x-box">
        <a-row :gutter="[16,16]">

          <a-col :span="18">
            <div class="document-main-left-box">
              <!-- 头部 -->
              <div class="document-main-left-header">
                <a-row>
                  <a-col :span="2">
                    <a @click="previousStepHandle"><a-icon type="left"/></a>
                  </a-col>
                  <a-col :span="20">
                    <span class="left-header-tag-item" v-for="(tag, index) in documentInfo.tags" :key="index">
                      {{ tag }}
                    </span>
                  </a-col>
                  <a-col :span="2">
                    <span class="header-datetime">{{ simpleTimestamp(documentInfo.datetime) }}</span>
                  </a-col>
                </a-row>
              </div>
              <!-- 头部 -->

              <!-- 内容区域 -->
              <div class="document-main-x-content-box">
                <div class="document-main-load-box" v-if="loadingStatus">
                  <a-spin size="large" tip="玩命加载中..."/>
                </div>
                <div class="document-main-content-box" v-else>
                  <component v-bind:is="document" key="document"
                             ref="documentMainContentBox" v-highlight></component>
                </div>
              </div>
              <!-- 内容区域 -->

            </div>
          </a-col>

          <a-col :span="6">
            <div class="document-main-right-box">
              <div class="document-main-catalogue-title"><a-icon type="align-left"/> 目录</div>
              <div class="main-catalogue-empty" v-if="catalogueLoadingStatus">
                <a-spin size="large" tip="玩命加载中..."/>
              </div>
              <a-anchor :offsetTop="20" v-else>
                <a-anchor-link v-show="false"><div class="document-main-catalogue-title">目录</div></a-anchor-link>
                <a-anchor-link v-for="(catalogue, index) in catalogueList" :key="index"
                               :href="getMenuHrefHandle(catalogue)">
                  <!-- 一级标题 -->
                  <div slot="title" class="document-main-catalogue-item"
                       :class="'h-'+catalogue.indent" :title="catalogue.title">
                    {{ catalogue.title }}
                  </div>
                  <!-- 二级标题 -->
                  <a-anchor-link v-for="(catalogue, index) in catalogue.submenus"
                                 :key="index" :href="getMenuHrefHandle(catalogue)">
                    <div slot="title" class="document-main-catalogue-item"
                         :class="'h-'+catalogue.indent" :title="catalogue.title">
                      {{ catalogue.title }}
                    </div>
                    <!-- 三级标题 -->
                    <a-anchor-link v-for="(catalogue, index) in catalogue.submenus"
                                   :key="index" :href="getMenuHrefHandle(catalogue)">
                      <div slot="title" class="document-main-catalogue-item"
                           :class="'h-'+catalogue.indent" :title="catalogue.title">
                        {{ catalogue.title }}
                      </div>
                      <!-- 四级标题 -->
                      <a-anchor-link v-for="(catalogue, index) in catalogue.submenus"
                                     :key="index" :href="getMenuHrefHandle(catalogue)">
                        <div slot="title" class="document-main-catalogue-item"
                             :class="'h-'+catalogue.indent" :title="catalogue.title">
                          {{ catalogue.title }}
                        </div>
                        <!-- 五级标题 -->
                        <a-anchor-link v-for="(catalogue, index) in catalogue.submenus"
                                       :key="index" :href="getMenuHrefHandle(catalogue)">
                          <div slot="title" class="document-main-catalogue-item"
                               :class="'h-'+catalogue.indent" :title="catalogue.title">
                            {{ catalogue.title }}
                          </div>
                          <!-- 六级标题 -->
                          <a-anchor-link v-for="(catalogue, index) in catalogue.submenus"
                                         :key="index" :href="getMenuHrefHandle(catalogue)">
                            <div slot="title" class="document-main-catalogue-item"
                                 :class="'h-'+catalogue.indent" :title="catalogue.title">
                              {{ catalogue.title }}
                            </div>
                          </a-anchor-link>
                          <!-- 六级标题 -->
                        </a-anchor-link>
                        <!-- 五级标题 -->
                      </a-anchor-link>
                      <!-- 四级标题 -->
                    </a-anchor-link>
                    <!-- 三级标题 -->
                  </a-anchor-link>
                  <!-- 二级标题 -->
                </a-anchor-link>
                <!-- 一级标题 -->
              </a-anchor>
            </div>
          </a-col>

        </a-row>
      </div>

      <!-- 大图 -->
      <a-modal :title="null" :width="'100%'" :footer="null" :dialog-style="{ top: '0px' }"
               :visible="bigImgVisible" @cancel="bigImgVisible = false" :closable="false">
        <div class="image-viewer__wrapper">
          <div class="image-viewer__canvas" @click="bigImgVisible = false">
            <img :src="currentImgPath" alt=""/>
          </div>
        </div>
      </a-modal>
      <!-- 大图 -->



    </div>



  </div>
</template>

<script>
import common from "../../util/common";
import MainCardBar from "../../components/frontend/main/MainCardBar";
import 'highlight.js/styles/a11y-dark.css';
import 'github-markdown-css';

export default {
  name: "DocumentMain",
  data () {
    return {
      loadingStatus: true,
      catalogueLoadingStatus: true,
      documentInfo: {},
      document: undefined,
      catalogueList: [],
      bigImgVisible: false,
      currentImgPath: ''
    }
  },
  mounted() {
    this.loadingStatus = true
    let param = this.$route.params.id
    if (param) {
      this.documentInfo = JSON.parse(common.deciphering(param))
      if (this.documentInfo.path)
        this.document = () => import('../../assets/documents' + this.documentInfo.path)
    } else {
      this.$router.push('/')
    }
    this.loadingStatus = false

    setTimeout(() => {
      let ele = this.$refs.documentMainContentBox
      if (ele) this.loadCatalogueList(ele)
    }, 1000)
  },
  methods: {
    loadCatalogueList(ele) {
      this.catalogueLoadingStatus = true
      let titles = { value: '' }
      let anchors = ele.$el.querySelectorAll("h1,h2,h3,h4,h5,h6")
      titles.value = Array.from(anchors).filter((title) => !!title.innerText.trim())
      const hTags = Array.from(new Set(titles.value.map((title) => title.tagName))).sort()
      titles.value.map((el => {el.setAttribute('id', common.guid())}))

      let list = common.catalogueListHandle(titles.value, hTags)
      let top = { id: 0, pid: 0 }
      common.completeSubmenus(top, list)
      this.catalogueList = top.submenus
      this.$nextTick(() => { this.catalogueLoadingStatus = false })

      let imgList = ele.$el.querySelectorAll("img")
      const that = this
      imgList.forEach(img => {
        img.onclick = (e) => {
          that.bigImgVisible = true;
          this.currentImgPath = e.target.currentSrc;
        }
      })
    },
    getMenuHrefHandle(catalogue) {return window.location.href + '#' + catalogue.targetId},
    previousStepHandle () { this.$router.push('/') },
    timestampShow (timestamp) {return common.timestampShow(timestamp)},
    simpleTimestamp (timestamp) {return common.simpleTimestamp(timestamp)}
  },
  components: { MainCardBar }
}
</script>
<style scoped>
.document-main-box{width: 100%;height: auto;background: #EBF4FF;}
.document-main-box .document-main-x-box {
  width: 90%;height: auto;margin: 0 auto; padding: 3em 0;
}
.document-main-box .document-main-x-box .document-main-left-box{
  width: 100%;height: auto;background: #fff;
}
.document-main-box .document-main-x-box .document-main-right-box{
  width: 22%;height: 80vh;
  position: fixed;
}

/* 顶部 */
.document-main-x-box .document-main-left-box .document-main-left-header {
  width: 100%;
  border-bottom: #f9fafc solid 1px;
}
.document-main-left-box .document-main-left-header .left-header-tag-item{
  display: inline-block;
  margin-top: 10px;
  cursor: text;
  color: #e33e33;
  text-transform: capitalize;
  background: rgba(227,62,51,.1);
  font-size: 12px;
  padding: 2px 8px;
  margin-right: 5px;
  border-radius: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 120px;
}
.document-main-left-box .document-main-left-header .header-datetime{
  color: #606266;
  font-size: 20px;
  line-height: 42px;
}

.document-main-x-box .document-main-left-box .document-main-left-header a{
  color: #677788;
  line-height: 42px;
  font-size: 16px;
  margin: 0 1em;
  cursor: pointer;
  transition: .43s;
}
.document-main-x-box .document-main-left-box .document-main-left-header a:hover{
  color: #333333;
}
/* 顶部 */

/* 目录 */
.document-main-box .document-main-x-box .document-main-right-box .main-catalogue-empty{
  width: 100%;
  height: 300px;
  background: #fff;
  text-align: center;
  padding-top: 8em;
}
.document-main-box .document-main-x-box .document-main-right-box .document-main-catalogue-title,
.document-main-box .document-main-x-box .document-main-right-box .document-main-catalogue-item{
  display: block;
  width: 100%;
  font-weight: 600;
  color: #3d3d3d;
  background: #e9e9e9;
  padding: 0 16px;
  height: 38px;
  line-height: 38px;
  overflow: hidden;
  white-space:nowrap;
  -o-text-overflow:ellipsis;
  text-overflow:ellipsis;
}
.document-main-box .document-main-x-box .document-main-right-box .document-main-catalogue-item{
  background: none;
  font-weight: 500;
  cursor: pointer;
}
.document-main-box .document-main-x-box .document-main-right-box .document-main-catalogue-item:hover{
  color: #fc5531;
}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link-title-active > .document-main-catalogue-item{
  background: #f0f0f5;
  color: #fc5531;
}

.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-wrapper{max-height: 80vh !important;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-wrapper::-webkit-scrollbar{width: 4px;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-wrapper::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
  background: #c1c9c9;
}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-wrapper::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(87, 175, 187, 0.1);
  border-radius: 10px;
  background: #ededed;
}

.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-wrapper{padding: 0;margin: 0;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor{padding: 0 !important;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-ink{display: none !important;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-ink::before{
  content: none;background: none;
}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link{padding: 0;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .ant-anchor-link-title{margin: 0}

.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-1{padding-left: 2.5em;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-2{padding-left: 3.5em;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-3{padding-left: 4.5em;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-4{padding-left: 5.5em;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-5{padding-left: 6.5em;}
.document-main-box .document-main-x-box .document-main-right-box >>> .ant-anchor-link .h-6{padding-left: 7.5em;}
/* 目录 */





/* 内容 */
.document-main-box .document-main-x-content-box{
  width: 100%;
  min-height: 100vh;
  background: #fff;
  padding: 2em;
}
.document-main-box .document-main-x-content-box .document-main-load-box{
  width: 100%;
  height: 100vh;
  text-align: center;
  padding-top: 10rem;
  background: #fff;
}

/* 字体大小 */
.document-main-box .document-main-content-box >>> *{
  font-size: 16px;
}
/* 字体大小 */

/* 标题 */
.document-main-box .document-main-content-box >>> h1, .document-main-box .document-main-content-box >>> h2,
.document-main-box .document-main-content-box >>> h3, .document-main-box .document-main-content-box >>> h4,
.document-main-box .document-main-content-box >>> h5, .document-main-box .document-main-content-box >>> h6{
  margin-top: 2em;
  margin-bottom: .8em;
  font-weight: 600;
  line-height: 1.2;
  color: #1e2022;
}
.document-main-box .document-main-content-box >>> h1{font-size: 22.75px;}
.document-main-box .document-main-content-box >>> h2{font-size: 19.50px;}
.document-main-box .document-main-content-box >>> h3{font-size: 17.55px;}
.document-main-box .document-main-content-box >>> h4{font-size: 16.25px;}
.document-main-box .document-main-content-box >>> h5{font-size: 14.95px;}
.document-main-box .document-main-content-box >>> h6{font-size: 13.95px;}
/* 标题 */

/* 大标题 */
.document-main-box .document-main-content-box >>> h1:first-child{
  text-align: center;
  margin-top: 0;
  padding-bottom: .3em;
  border-bottom: #f9fafc solid 1px;
}
/* 大标题 */


/* 代码块 */
.document-main-box .document-main-content-box >>> pre{
  position: relative;
  background: #2b2b2b;
  font-size: 14px;
  border-radius: 4px;
  padding: 30px 0 0 0;
}
.document-main-box .document-main-content-box >>> pre code{
  display: block;
  width: 100%;
  height: auto;
  border-top: #ccc solid 1px;
}
.document-main-box .document-main-content-box >>> pre code::-webkit-scrollbar{height: 6px;}
.document-main-box .document-main-content-box >>> pre code::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
  background: #c1c9c9;
}
.document-main-box .document-main-content-box >>> pre code::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(87, 175, 187, 0.1);
  border-radius: 10px;
  background: #2b2b2b;
}
.document-main-box .document-main-content-box >>> pre::before{
  content: '';
  position: absolute;
  top: 9px;
  left: 10px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fc625d;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
  z-index: 1;
}
/* 代码块 */

/* 图片 */
.document-main-box .document-main-content-box >>> img{
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 100%;
}
/* 图片 */

/* 表格 */
.document-main-box .document-main-content-box >>> table {
  border: #e7eaf3 solid 1px;
}
.document-main-box .document-main-content-box >>> table tr th,
.document-main-box .document-main-content-box >>> table tr td{
  padding: .6em 1em;
  border: #e7eaf3 solid 1px;
}
/* 表格 */

/* 块引用 */
.document-main-box .document-main-content-box >>> blockquote{
  line-height: 26px;
  margin-bottom: 18px;
  background: #ecf8ff;
  border-left: 5px solid #50bfff;
  color: #606266;
  padding: 8px 15px;
  border-radius: 0 4px 4px 0;
}
/* 块引用 */


.image-viewer__wrapper{position: fixed;width:100%;height:100%;top: 0;right: 0;bottom: 0;left: 0;}
.image-viewer__wrapper .image-viewer__canvas{
  width: 100%;height: 100%;display: flex;justify-content: center;align-items: center;
}
.image-viewer__wrapper .image-viewer__canvas img{
  overflow-clip-margin: content-box;transform: scale(1) rotate(0deg);margin-left: 0;margin-top: 0;max-height: 100%;max-width: 100%;
}

</style>
